import React, { useState, useEffect } from "react"
import { Container, Nav, Navbar, NavDropdown, NavLink } from "react-bootstrap"
import { Link, navigate } from "gatsby"
import { useLocation } from "@reach/router"
import "./NavBarTop.scss"
import Button from "../Button/Button"
import DropDown from "./DropDown"
import { DropDownIcon } from "./SVGs"
export default function NavBarTop() {
  const [isShrunk, setShrunk] = useState(false)
  const [isShown, setIsShown] = useState(false)
  const [expand, setExpand] = useState(false)
  const [caseStudy, setcaseStudy] = useState(false)
  const [width, setWidth] = useState(null)
  const useLoc = useLocation()

  useEffect(() => {
    setIsShown(false)
    width < 992 && setExpand(false)
    if (
      useLoc.pathname == "/case-study/automotivesaas" ||
      useLoc.pathname == "/case-study/automotivesaas/" ||
      useLoc.pathname.includes("career-details")
    ) {
      setcaseStudy(true)
    } else if (
      useLoc.pathname == "/case-study/ohmconnect" ||
      useLoc.pathname == "/case-study/ohmconnect/"
    ) {
      setcaseStudy(true)
    } else {
      setcaseStudy(false)
    }
  }, [useLoc.pathname])

  useEffect(() => {
    const handler = () => {
      setShrunk(isShrunk => {
        if (
          !isShrunk &&
          (document.body.scrollTop > 20 ||
            document.documentElement.scrollTop > 20)
        ) {
          return true
        }

        if (
          isShrunk &&
          document.body.scrollTop < 4 &&
          document.documentElement.scrollTop < 4
        ) {
          return false
        }

        return isShrunk
      })
    }
    window.addEventListener("scroll", handler)
    return () => window.removeEventListener("scroll", handler)
  }, [])

  const updateDimensions = () => {
    if (window !== "undefined") {
      setWidth(window.innerWidth)
    }
  }
  useEffect(() => {
    if (window !== "undefined") {
      setWidth(window.innerWidth)
    }
  }, [])
  useEffect(() => {
    if (window !== "undefined") {
      window.addEventListener("resize", updateDimensions)
      return () => window.removeEventListener("resize", updateDimensions)
    }
  }, [width])

  useEffect(() => {
    // const body = document.querySelector("body")
    document.body.classList.add("disable-scroll")
    ////body.style.overflow = expand ? "hidden" : "hidden"
  }, [expand])

  const ServicesClickHandler = () => {
    width > 992 && navigate("/services")
  }
  return (
    <div>
      <Navbar
        expand="lg"
        fixed="top"
        className={!!isShrunk ? "shrink-nav" : ""}
        expanded={expand}
      >
        <Container
          className={expand ? "px-3 nav-mobile-bg" : "px-3"}
          style={{ position: "relative" }}
        >
          {isShown && width > 992 && (
            <div className="">
              <DropDown
                onMouseLeave={() => setIsShown(false)}
                onTouchEnd={() => setIsShown(false)}
              />
            </div>
          )}

          <Link to="/" className="navbar-brand">
            <img
              src={
                caseStudy && !isShrunk && width > 992
                  ? `/CF-logo.svg`
                  : `/CF-logo-dark.svg`
              }
              alt="logo"
              className="img-fluid"
            />
          </Link>
          <Navbar.Toggle
            aria-controls="navbarScroll"
            onClick={() => {
              setExpand(!expand)
              setIsShown(false)
            }}
          />
          <Navbar.Collapse id="navbarScroll">
            <div className={"mobile-nav-wrapper"}>
              <Nav className="mx-auto nav" navbarScroll>
                <div>
                  <NavDropdown
                    onMouseEnter={() => setIsShown(true)}
                    onTouchStart={() => setIsShown(!isShown)}
                    onClick={ServicesClickHandler}
                    title={
                      <span>
                        {" "}
                        Services{" "}
                        <DropDownIcon
                          fill={
                            useLoc.pathname.includes("career-details")
                              ? "#fff"
                              : "#121212"
                          }
                        />{" "}
                      </span>
                    }
                    id={
                      caseStudy && !isShrunk && width > 992
                        ? `navbarScrollingDropdown`
                        : "false"
                    }
                    ///Received `false` for a non-boolean attribute `id`
                    ///id is non boolean so we cant pass false but we pass string "false"
                  >
                    {expand && (
                      <div>
                        <NavLink
                          onClick={() => navigate("/product-strategy")}
                          style={{ fontSize: "16px" }}
                        >
                          Product Strategy
                        </NavLink>
                        <NavLink
                          onClick={() => navigate("/product-design")}
                          style={{ fontSize: "16px" }}
                        >
                          Product Design
                        </NavLink>
                        <NavLink
                          onClick={() =>
                            navigate("/custom-software-development")
                          }
                          style={{ fontSize: "16px" }}
                        >
                          Custom Software Development
                        </NavLink>
                        <NavLink
                          onClick={() => navigate("/data-engineering")}
                          style={{ fontSize: "16px" }}
                        >
                          Data Engineering
                        </NavLink>
                      </div>
                    )}
                  </NavDropdown>
                </div>
                <Link
                  to="/portfolio"
                  className={
                    caseStudy && !isShrunk && width > 992
                      ? `nav-link case-study`
                      : `nav-link`
                  }
                  onClick={() => setExpand(false)}
                >
                  Portfolio
                </Link>
                <Link
                  to="/about-us"
                  onClick={() => setExpand(false)}
                  className={
                    caseStudy && !isShrunk && width > 992
                      ? `nav-link case-study`
                      : `nav-link`
                  }
                >
                  About Us
                </Link>

                <Link
                  to="/blog"
                  onClick={() => setExpand(false)}
                  className={
                    caseStudy && !isShrunk && width > 992
                      ? `nav-link case-study`
                      : `nav-link`
                  }
                >
                  Blogs
                </Link>
                <Link
                  to="/careers"
                  className={
                    caseStudy && !isShrunk && width > 992
                      ? `nav-link case-study`
                      : `nav-link`
                  }
                  onClick={() => setExpand(false)}
                >
                  Careers
                </Link>
              </Nav>

              <div className="menu-btn">
                <Button
                  href="/contact"
                  text="CONTACT US"
                  bgClr={
                    caseStudy && !isShrunk && width > 992
                      ? `#ffffff`
                      : `#121212`
                  }
                  border={
                    caseStudy && !isShrunk && width > 992
                      ? `#ffffff`
                      : `#121212`
                  }
                  clr={
                    caseStudy && !isShrunk && width > 992
                      ? `#2f2f2f`
                      : `#ffffff`
                  }
                  variant="fill"
                  className="nav-btn"
                  onClick={() => setExpand(false)}
                />
              </div>
            </div>
            {expand && (
              <div className="nav-social">
                <div className="social-icons-nav mb-1">
                  <a
                    href="https://www.linkedin.com/company/codefulcrum/"
                    target="_blank"
                    className="social-icon"
                    rel="noreferrer"
                  >
                    <img
                      src="/homepage/linkedin.svg"
                      alt=""
                      className="img-fluid"
                    />
                  </a>

                  <a
                    href="https://instagram.com/codefulcrum_?igshid=YmMyMTA2M2Y="
                    target="_blank"
                    className="social-icon"
                    rel="noreferrer"
                  >
                    <img
                      src="/homepage/instagram.svg"
                      alt=""
                      className="img-fluid"
                      id="insta"
                    />
                  </a>
                  <a
                    href="https://www.facebook.com/codeFulcrum/"
                    target="_blank"
                    className="social-icon"
                    rel="noreferrer"
                  >
                    <img
                      src="/homepage/facebook.svg"
                      alt=""
                      className="img-fluid"
                    />
                  </a>
                </div>
              </div>
            )}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}
